import {Component, Input} from "@angular/core";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {Icon} from "@util/icon";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";

@Component({
   templateUrl: "mor-cell.html",
   styleUrls: ["mor-cell.scss"],
   selector: "vsan-mor-cell",
})
export class MorCell {
   @Input()
   objectId: string | ManagedObject;

   private _shape: string;

   @Input()
   set shape(value: string) {
      this._shape = value;
      this.iconTitle = Icon.getDefaultVsanIconTitle(value);
   }

   get shape(): string {
      return this._shape ? this._shape : ManagedObjectUtils.getObjectTypeIcon(this.objectId);
   }

   @Input()
   name: string;

   /**
    * When specified attaches additional text after the name
    */
   @Input()
   suffix: string = "";

   iconTitle: string = "";

   constructor(private navigationService: NavigationService) {
   }

   getTitle(): string {
      return `${this.iconTitle} ${this.getName()}`;
   }

   getName(): string {
      return this.suffix ? `${this.name} ${this.suffix}` : this.name;
   }

   navigateTo(): void {
      this.navigationService.navigateTo(PlatformExtension.LAST_VISITED_PAGE, this.objectId);
   }
}